body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.card-container{
    display: flex;
    flex-direction: column;
    background-color: #404040;
    border-radius: 20px;
    box-shadow: 6px 9px 18px rgba(0, 0, 0, 0.35);
    padding: 20px;
    cursor: pointer;
}
.card-img{
    max-width: 200px;
    height: auto;
}
.card-info *{
    margin-block: 5px;
    color: #95b6f3;
    text-shadow: 4px 4px 12px rgba(0, 0, 0, 0.5);
}

.card-container:hover {
    -webkit-animation: bounce 1s ease forwards;
            animation: bounce 1s ease forwards;
  }
  @-webkit-keyframes bounce {
      0% { -webkit-transform:translateY(0%); transform:translateY(0%); }
      40% { -webkit-transform:translateY(-13%) scale(1.05); transform:translateY(-13%) scale(1.05); }
      70% { -webkit-transform:translateY(10%) scale(0.95); transform:translateY(10%) scale(0.95); }
      100% { -webkit-transform:translateY(0); transform:translateY(0); }
  }
  @keyframes bounce {
      0% { -webkit-transform:translateY(0%); transform:translateY(0%); }
      40% { -webkit-transform:translateY(-13%) scale(1.05); transform:translateY(-13%) scale(1.05); }
      70% { -webkit-transform:translateY(10%) scale(0.95); transform:translateY(10%) scale(0.95); }
      100% { -webkit-transform:translateY(0); transform:translateY(0); }
  }
.robo-cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-row-gap: 50px;
    row-gap: 50px;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    padding: 50px;
  }
  
.search-bar{
    outline: 0;
    border: none;
    font-size: 24px;
    color: #444444;
    background: aliceblue;
    padding: 18px;
    border-radius: 25px;
    border: 2px solid rgb(146, 146, 218);
}
@media (max-width:575px){
    .search-bar{
        padding: 16px;
        font-size: 13px;
    }
}
.container{
    /* max-width: 1440px; */
    margin: 0 auto;
  }
.header{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}
